<template>
	<div id="nav">
		<div class="nav">
			<div class="swiper-containerNav">
				<div class="swiper-wrapper navList">
					<div class="swiper-slide" v-for="(item,index) in list">{{item.title}}</div>
				</div>
			</div>
		</div>
		<div class="menu">777</div>
	</div>
</template>

<script>
	import axios from 'axios'
	import "../../../node_modules/swiper/dist/css/swiper.css"
	import Swiper from "swiper"

	export default {
		data() {
			return {
				curIndex:0,
				news:[],
				list: [{
						title: '头条',
						type: ''
					},
					{
						title: '娱乐',
						type: 'pcent'
					},
					{
						title: '科技',
						type: 'pctech'
					},
					{
						title: '体育',
						type: ''
					},
					{
						title: '头条',
						type: ''
					},
					{
						title: '头条',
						type: ''
					},
					{
						title: '头条',
						type: ''
					},
					{
						title: '头条',
						type: ''
					},
					{
						title: '头条',
						type: ''
					},
					{
						title: '头条',
						type: ''
					},
					{
						title: '头条',
						type: ''
					}
				]
			}
		},
		mounted() {
			var vm=this;
			var mySwiper = new Swiper('.swiper-containerNav', {
				freeMode: true, //变为自由模式
				slidesPerView: 'auto',
				on:{
					click(){
						vm.curIndex=this.clickedIndex;
						this.setTransition(370);
						var el=this.slides[this.clickedIndex];
						var elpos=el.offsetLeft+el.clientWidth/2;
						var swiperWideth=this.wrapperEl.clientWidth;
						var maxDis=this.maxTranslate();
						var maxPos=-maxDis+swiperWideth/2;
						if(elpos<swiperWideth/2){
							this.setTranslate(0)
						}
						else if(elpos>maxPos){
							this.setTranslate(maxDis)
						}
						else{
							this.setTranslate(swiperWideth/2-elpos)
						}
						if(vm.curIndex===0){
							vm.$router.push('/Home/index/list')
						}
						else{
							vm.$router.push('/Home/index/'+vm.list[vm.curIndex].type)
						}
					}
				}
			})
		}
	}
</script>

<style lang="scss">
	$rem:25;
	
	#nav{
		width: 100%;
		height: 40/$rem+rem;
		display: flex;
		position: fixed;
		top: 35/$rem+rem;
		left: 0;
		.nav{
			width: 90%;
			margin-left: 10/$rem+rem;
			overflow: hidden;
			.navList{
				div{
					width: 50/$rem+rem;
					font-size: 14/$rem+rem;
					line-height: 32/$rem+rem;
					text-align: center;
				}
			}
		}
		.menu{
			width: 10%;
			font-size: 14/$rem+rem;
		}
		
	}
</style>


