<template>
	<div class="video">
		<ul>
			<li v-for="item in list">
				<div>
					<p>{{item.title}}</p>
					<p>{{item.intro}}</p>
				</div>
				<video :src="item.video_url" controls="controls" preload="none" :poster="item.thumb"></video>
				<p>
					<span>新浪娱乐</span>
					<i>{{item.video_size}}</i>
					<a href="#"></a>
				</p>
			</li>
		</ul>
	</div>
</template>

<script>
	export default{
		data(){
			return{
				list:[]
			}
		},
		created(){
			this.$jsonp("http://cre.mix.sina.com.cn/api/v3/get?callback&callback=callback&_=1544065823938",{
				callbackName:'callback'
			}).then((res)=>{
				//console.log(res.data)
				this.list=res.data.filter((item,index)=>{
					return index>4
				})
			})
		}
	}
</script>

<style lang="scss">
	$rem:25;
	
	.video{
		width: 100%;
		height: 100%;
		ul{
			li{
				padding: 7.5/$rem+rem 0 0 8.5/$rem+rem;
				line-height: 21/$rem+rem;
				width: 100%;
			    border-bottom: 1/$rem+rem solid #dbdbdb;
			    background: #f0f0f0;
				&>div{
					padding: 0 12/$rem+rem;
					overflow: hidden;
				    white-space: nowrap;
				    text-overflow: ellipsis;
					p:first-child{
						font-size: 15/$rem+rem;
						color: #646464;
					}
					p:last-child{
						font-size: 12/$rem+rem;
    					color: #646464;
					}
				}
				&>video{
					width: 100%;
				}
				&>p{
					text-align: left;
					font-size: 12/$rem+rem;
					color: #646464;
					padding-bottom: 10/$rem+rem;
					span{
						
					}
					i{
						background: url(../../../static/images/commit.svg) no-repeat left center;
						padding-left: 20/$rem+rem;
						margin-left: 34/$rem+rem;
					}
					a{
						float: right;
						width: 15/$rem+rem;
						height: 15/$rem+rem;
						background: url(../../../static/images/share2.svg) no-repeat;
						margin: 3/$rem+rem 20/$rem+rem 0 0;
					}
				}
			}
		}
	}
</style>


