import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/page/Home'
import index from '@/components/page/index'
import video from '@/components/page/video'
import like from '@/components/page/like'
import user from '@/components/page/user'
import login from '@/components/page/login'
import register from '@/components/page/register'

import list from '@/components/twoPage/list'
import newlist from '@/components/twoPage/newlist'

Vue.use(Router)

export default new Router({
	mode:'history',
  routes: [
    {
      path: '/',
      redirect:{'name':'home'}
    },
    {
    	path:'/home',
    	name:'home',
    	component:Home,
    	redirect:{'name':'index'},
    	children:[
			{
				path:'/Home/index',
				name:'index',
				component:index,
				redirect:{'name':'list'},
				children:[
					{
						path:'/Home/index/list',
						name:'list',
						component:list
					},
					{
						path:'/Home/index/:type',
						name:'newlist',
						component:newlist
					}
				]
			},
			{
				path:'/Home/video',
				name:'video',
				component:video
			},
			{
				path:'/Home/like',
				name:'like',
				component:like
			}
    	]
    },
    {
    	path:'/user',
		name:'user',
		component:user
	},
	 {
    	path:'/login',
		name:'login',
		component:login
	},
	 {
    	path:'/register',
		name:'register',
		component:register
	}
  ]
})



