<template>
	<div id="banner">
		<div class="swiper-container">
			<div class="swiper-wrapper">
				<div class="swiper-slide" v-for="item in list">
					<img :src="item.thumb" class="imgs"/>
				</div>
			</div>
			<!-- 如果需要分页器 -->
			<div class="swiper-pagination"></div>
		</div>
	</div>
</template>

<script>
	import axios from "axios"
	import "../../../node_modules/swiper/dist/css/swiper.css"
	import Swiper from "swiper"

	export default {
		data() {
			return {
				list: []
			}
		},
		created(){
			axios.get("/xl/api/v3/get?_=1543987535726").then((res)=>{
				//console.log(res)
				this.list=res.data.data.filter((item,index)=>{
					return index<5
				})
				//console.log(this.list)
			})
		},
		watch:{
			list(){
				this.$nextTick(() => {
				var mySwiper = new Swiper('.swiper-container', {
					loop: true, // 循环模式选项
					// 如果需要分页器
					pagination: {
						el: '.swiper-pagination',
					},
					autoplay: {
						delay: 2000,
						disableOnInteraction: false,
					}
				})
			})
			}
		}
	}
</script>

<style lang="scss">
	$rem:25;
	
	#banner{
		.imgs{
			width: 100%;
			height: 135/$rem+rem;
		}	
	}
</style>


