import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

var vm=new Vue

var store=new Vuex.Store({
	state:{
		list:[]
	},
	mutations:{
		getdata(state,res){
			state.list=res.filter((item)=>{
				return item.pic[0]
			});
		}
	},
	actions:{
		getData(context){
			vm.$jsonp("http://temp.163.com/special/00804KV1/post1603_api_all.js?_=1544011390895",{
				callbackName:"callback"
			}).then((res)=>{
				//console.log(res.data)
				context.commit("getdata",res.data)
			})
		}
	}
})

export default store


