<template>
	<div class="user">
		<header>
			<div class="yellow">
				<h4 class="myusers" v-if="flags">用户{{myphone}},已登录</h4>
				<div class="white">
					<div class="circular">
						<router-link to="/login" v-if="flag"><img src="../../../static/images/logogray.png"/></router-link>
						<img src="../../../static/images/logogray.png" v-if="flags" @click="quit"/>
					</div>
					<div class="dl" v-if="flag"><span></span>点击头像登陆</div>
					<div class="welcome" v-if="flags"><span></span>点击头像退出登录</div>
				</div>
			</div>
		</header>
		<footer id="footer">
			<ul class="foot">
				<li>
					<router-link to="/Home/index"><i class="fa fa-home fa-2x"></i>首页</router-link>
				</li>
				<li>
					<router-link to="/Home/video"><i class="fa fa-video-camera fa-2x"></i>视频</router-link>
				</li>
				<li>
					<router-link to="/Home/like"><i class="fa fa-plus-square fa-2x"></i>订阅</router-link>
				</li>
				<li>
					<router-link to="/user"><i class="fa fa-user fa-2x"></i>个人</router-link>
				</li>
			</ul>
		</footer>
	</div>
</template>

<script>
	export default{
		data(){
			return{
				flag:true,
				flags:false,
				myphone:""
			}
		},
		created(){
			if(sessionStorage.getItem("myPhone")){
				this.myphone=sessionStorage.getItem("myPhone")
				this.flag=false;
				this.flags=true;
			}
		},
		methods:{
			quit(){
				sessionStorage.clear("myPhone");
				this.flag=true;
				this.flags=false;
			}
		}
	}
</script>

<style lang="scss">
	$rem:25;
	
	.user{
		header{
			.yellow{
				width: 100%;
				height: 274/$rem+rem;
				background: #F8B62B;
				overflow: hidden;
				position: relative;
				.myusers{
					width: 100%;
					height: 30/$rem+rem;
					text-align: right;
					padding-right: 10/$rem+rem;
					color: white;
					font-size: 16/$rem+rem;
					line-height: 30/$rem+rem;;
					font-style: italic;
					font-family: "楷体";
					margin-right: 10/$rem+rem;
					background: rgba(0,0,0,.3);
				}
				.white{
					width: 500/$rem+rem;
					height: 300/$rem+rem;
					background: white;
					border-radius: 70%;
					position: absolute;
					top: 90/$rem+rem;
					left: 50%;
					margin-left: -70/$rem+rem;
					left: 0;
					.circular{
						padding-top: 10/$rem+rem;
						width: 94/$rem+rem;
						height: 84/$rem+rem;
						border-radius: 50%;
						background: white;
						text-align: center;
						position: absolute;
						margin-left: -42/$rem+rem;
						margin-top: -42/$rem+rem;
						left: 50%;
						img{
							width: 61/$rem+rem;
							height: 45/$rem+rem;
						}
					}
					.dl,.welcome{
						position: absolute;
						top: 42/$rem+rem;
						left: 50%;
						width: 100/$rem+rem;
						height: 25/$rem+rem;
						background: #F8B62B;
						border-radius: 4/$rem+rem;;
						font-size: 11/$rem+rem;
						margin-left: -45/$rem+rem;
						line-height: 25/$rem+rem;
						text-align: center;
						span{
							display: block;
							position: absolute;
							margin-top: -10/$rem+rem;
						    left: 50%;
						    transform: translateX(-50%);
						    width: 0;
						    height: 0;
						    border: 5/$rem+rem solid transparent;
						    border-color: transparent transparent #F8B62B transparent;
						}
					}
				}
			}
		}
	}
	
</style>


