<template>
	<div id="indexList" v-scroll>
		<bannerList></bannerList>
		<div class="list">
			<ul>
				<li v-for="(item,index) in list">
					<div>
						
						<h4>{{item.title}}</h4>
						<p>
							<span>{{item.source}}</span>
							<span>{{item.ptime}}</span>
						</p>
					</div>
					<img :src="item.pic[0]"/>
					<i @click="del(index)">X</i>
				</li>
			</ul>
		</div>		
	</div>
</template>

<script>
	import bannerList from './banner'
	import {mapState,mapActions} from 'vuex'

	export default{
		created(){
			this.getData()
		},
		computed:{
			...mapState(["list"])
		},
		methods:{
			...mapActions(["getData"]),
			del(i){
				this.list.splice(i,1)
			}
		},
		components:{
			bannerList
		}
	}
</script>

<style lang="scss">
	$rem:25;
	
	#indexList{
		width: 100%;
		position: fixed;
	    left: 0;
	    right: 0;
	    top: 75/$rem+rem;
	    bottom: 49/$rem+rem;
	    width: 100%; 
	    overflow: scroll;
		.list{
			ul{
				li{
					width: 100%;
					padding: 9/$rem+rem 12/$rem+rem 4/$rem+rem;
    				border-bottom: 1/$rem+rem solid #dbdbdb;
    				background: #f0f0f0;
    				overflow: hidden;
    				&>div{
    					width: 217.5/$rem+rem;
    					float: left;
    					h4{
    						font-size: 14/$rem+rem;
    						font-weight: normal;
    						padding-right: 15/$rem+rem;
    					}
    					p{
    						span{
    							font-size: 12/$rem+rem;
    							color: #646464;
    							margin-right: 15/$rem+rem;
    						}
    					}
    				}
    				&>img{
    					width: 110/$rem+rem;
    					float: left;
    				}
    				&>i{
    					display: inline-block;
					    font-size: 12/$rem+rem;
					    border: 1/$rem+rem solid #909090;
					    text-align: center;
					    line-height: 12/$rem+rem;
					    width: 12/$rem+rem;
					    height: 12/$rem+rem;
					    border-radius: 50%;
					    color: #909090;
					    overflow: hidden;
					    margin-left: 5/$rem+rem;
    				}
				}
			}
		}
	}
</style>


