<template>
	<div class="register">
		<header>
			<router-link to="/login" class="go_user">手机号注册</router-link>
		</header>
		<section>
			<ul class="list">
				<li>
					<input type="text" placeholder="请输入手机号" v-model="phone"/>
					<span class="getNum" @click="show">获取验证码</span>
					<i class="getNums" v-show="flag" @click="getNums">{{randnum}}</i>
				</li>
				<li>
					<input type="text" placeholder="请输入验证码" v-model="num"/>
				</li>
				<li>
					<input type="text" placeholder="请输入密码(6-12位数字、字母、符号)" v-model="psd"/>
				</li>
				<li>
					<input type="text" placeholder="确认密码" v-model="twopsd"/>
				</li>
			</ul>
			<div class="btn">
				<button @click="register">完成</button>
			</div>
		</section>
	</div>
</template>

<script>
	import axios from 'axios'
	
	export default{
		data(){
			return{
				flag:false,
				phone:"",
				num:"",
				psd:"",
				twopsd:"",
				nums:[],
				randnum:"",
				regPhone:"/^1[3|4|5|7|8][0-9]{9}$/",
				regPsd:"/^[0-9a-zA-Z!@#$%^&*]{6,12}$/",
				arr:[]
			}
		},
		created(){
			
		},
		methods:{
			//求随机数
			rand(min,max){
				return Math.round(Math.random()*(max - min) + min);
			},
			//显示验证码
			show(){
				this.flag=true
				this.getNums()
			},
			//添加验证码
			getNums(){
				this.nums.length=0;
				this.randnum="";
				for(let i=0;i<4;i++){
					this.nums.push(this.rand(0,9))
				}
				this.randnum+=this.nums.join("")
			},
			//判断手机号是否注册
			getData(){
				axios.get("http://localhost:3000/list").then((res)=>{
					this.arr=res.data.filter((item)=>{
						return item.phone.includes(this.phone)
					})
				})
			},
			//判断登录
			register(){
				this.getData()
				if(this.phone===""||this.psd===""||this.num===""||this.twopsd===""){
					alert("请先填写完注册信息");
					return
				}
				else if(this.arr.length>=1){
					alert("手机号已被注册，请重新输入");
					return
				}
				else if(!(eval(this.regPhone).test(this.phone))){
					alert("手机号不正确，请重新输入");
					return
				}
				else if(!(eval(this.regPsd).test(this.psd))){
					alert("密码格式不正确，请重新输入")
					return
				}
				else if(this.psd!==this.twopsd){
					alert("两次密码不一致，请重新输入")
					return
				}
				else if(this.num!==this.randnum){
					alert("验证码不正确，请重新输入")
					return
				}
				else{
					let data={
						"phone":this.phone,
						"password":this.psd
					}
					axios.post("http://localhost:3000/list",data,{
						headers:{
							"content-type":"application/json"
						}
					}).then(()=>{
						alert("注册成功"),
						this.$router.push("/login")
					})
				}
			}
		}
	}
</script>

<style lang="scss">
	$rem:25;
	
	.register{
		width: 100%;
		height: 100%;
		display: flex;
		flex-direction: column;
		header{
			width: 100%;
			height: 43.984/$rem+rem;
			background: white;
			padding: 0 12/$rem+rem;
			display: flex;
			align-items: center;
		}
		section{
			flex: 1;
			height: 100%;
			background: #f4f4f4;
			.list{
				margin-top: 50/$rem+rem ;
				li:first-child{
					border-top: 1/$rem+rem solid #dbdbdb;
					.getNum{
						position: absolute;
						right: 0;
						top: 0;
						width: 98/$rem+rem;
						height: 100%;
						background: #000;
						text-align: center;
						line-height: 49/$rem+rem;
						color: #f8b62b;
						background: #777;
						font-size: 14/$rem+rem;
					}
					.getNums{
						position: absolute;
						right: 0;
						top: 0;
						z-index: 9;
						width: 98/$rem+rem;
						height: 100%;
						background: #000;
						text-align: center;
						line-height: 49/$rem+rem;
						color: skyblue;
						background: #F0F0F0;
					}
				}
				li{
					width: 100%;
					height: 49/$rem+rem;
					border-bottom: 1/$rem+rem solid #dbdbdb;
					position: relative;
					input{
						width: 100%;
						height: 100%;
						padding-left: 10/$rem+rem;
						border: 0;
						position: absolute;
						left: 0;
						top: 0;
					}
				}
			}
			.btn{
				width: 100%;
				text-align: center;
				margin-top: 100/$rem+rem;
				button{
					width: 349/$rem+rem;
					height: 35.5/$rem+rem;
					border: 1/$rem+rem solid #dbdbdb;
					border-radius: 4/$rem+rem;
					background: white;
				}
			}
		}
	}
</style>


