<template>
	<div id="newlist">
		<ul>
			<li v-for="(item,index) in news">
				<div>
					<h4>{{item.mintro}}</h4>
					<p>
						<span>新浪娱乐</span>
					</p>
				</div>
				<img :src="item.thumb" />
				<i @click="del(index)">X</i>
			</li>
		</ul>
	</div>
</template>

<script>
	import axios from 'axios'

	export default {
		data() {
			return {
				news: [],
				type: ""
			}
		},
		created() {
			this.getData()
		},
		watch: {
			$route: {
				handler(n) {
					this.type = n.params.type;
					this.getData()
				},
				immediate: true
			}
		},
		methods: {
			getData() {
				this.$jsonp("http://cre.mix.sina.com.cn/api/v3/get?cateid=1&cre=tianyi&mod=" + this.type + "&merge=3&statics=1&length=20&up=2&down=0&fields=media&_=1544022363634").then((res) => {
					this.news=res.data.filter((item)=>{
						return item.mintro
					})
				})
				/*axios.get("/wy3/nc/article/list/T1467284926140/0-20.html").then((res)=>{
					console.log(res)
				})*/
			},
			del(index) {
				this.news.splice(index, 1)
			}
		}
	}
</script>

<style lang="scss">
	$rem:25;
	#newlist {
		ul {
			li {
				width: 100%;
				padding: 9/$rem+rem 12/$rem+rem 4/$rem+rem;
				border-bottom: 1/$rem+rem solid #dbdbdb;
				background: #f0f0f0;
				overflow: hidden;
				&>div {
					width: 217.5/$rem+rem;
					float: left;
					h4 {
						font-size: 14/$rem+rem;
						font-weight: normal;
						padding-right: 15/$rem+rem;
					}
					p {
						span {
							font-size: 12/$rem+rem;
							color: #646464;
							margin-right: 15/$rem+rem;
						}
					}
				}
				&>img {
					width: 110/$rem+rem;
					float: left;
				}
				&>i {
					display: inline-block;
					font-size: 12/$rem+rem;
					border: 1/$rem+rem solid #909090;
					text-align: center;
					line-height: 12/$rem+rem;
					width: 12/$rem+rem;
					height: 12/$rem+rem;
					border-radius: 50%;
					color: #909090;
					overflow: hidden;
					margin-left: 5/$rem+rem;
				}
			}
		}
	}
</style>


