<template>
	<div class="login">
		<header>
			<router-link to="/user" class="go_user">返回</router-link>
			<router-link to="/register" class="go_register">注册</router-link>
		</header>
		<section>
			<div class="log">
				<img src="../../../static/images/logo.png"/>
			</div>
			<div class="list">
				<p>
					<i class="fa fa-user"></i>
					<input type="text" placeholder="手机号" v-model="phone"/>
				</p>
				<p>
					<i class="fa fa-user"></i>
					<input type="password" placeholder="密码" v-model="psd"/>
				</p>
			</div>
			<div class="btn">
				<button @click="login">登陆</button>
			</div>
		</section>
	</div>
</template>

<script>
	import axios from 'axios'
	
	export default{
		data(){
			return{
				phone:"",
				psd:"",
				arr:[]
			}
		},
		methods:{
			login(){
				axios.get("http://localhost:3000/list").then((res)=>{
					//console.log(res.data)
					this.arr=res.data.filter((item)=>{
						return item.phone.includes(this.phone)
					})
					//console.log(this.arr)
					if(this.phone===""||this.psd===""){
						alert("请填写手机号和密码在登录")
						return
					}
					if(this.arr.length===0){
						alert("此手机号还未注册")
						return
					}
					else if(this.psd!==this.arr[0].password){
						alert("密码输入错误，请重新输入")
						return
					}
					else{
						alert("登陆成功")
						sessionStorage.setItem("myPhone",this.phone)
						this.$router.push("/user")
					}
				})
			}
		}
	}
</script>

<style lang="scss">
	$rem:25;
	
	.login{
		width: 100%;
		height: 100%;
		display: flex;
		flex-direction: column;
		header{
			width: 100%;
			height: 43.984/$rem+rem;
			background: white;
			padding: 0 12/$rem+rem;
			display: flex;
			justify-content: space-between;
			align-items: center;
			.go_user{
				
			}
			.go_register{
				
			}
		}
		section{
			flex: 1;
			height: 100%;
			background: #f4f4f4;
			.log{
				width: 100%;
				text-align: center;
				margin-top: 50/$rem+rem;
				margin-bottom: 50/$rem+rem;
				img{
					width: 147.474/$rem+rem;
					height: 147.474/$rem+rem;
				}
			}
			.list{
				p:first-child{
					border-top: 1/$rem+rem solid #dbdbdb
				}
				p{
					width: 100%;
					height: 49/$rem+rem;
					border-bottom: 1/$rem+rem solid #dbdbdb;
					position: relative;
					i{
						position: absolute;
						left: 20/$rem+rem;
						top: 50%;
						margin-top: -15/$rem+rem;
						z-index: 9;
						display: inline-block;
						width: 30/$rem+rem;
						height: 30/$rem+rem;
					}
					input{
						width: 100%;
						height: 100%;
						padding-left: 50/$rem+rem;
						border: 0;
						position: absolute;
						left: 0;
						top: 0;
					}
				}
			}
			.btn{
				width: 100%;
				text-align: center;
				margin-top: 20/$rem+rem;
				button{
					width: 349/$rem+rem;
					height: 35.5/$rem+rem;
					border: 1/$rem+rem solid #dbdbdb;
					border-radius: 4/$rem+rem;
					background: white;
				}
			}
		}
	}
</style>


